* {
    font-family: 'Roboto' sans-serif; }

@mixin button {
    box-shadow: inset 0 -3px rgba(0,0,0, 0.12);
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    color: #FFFFFF; }

.lable {
    padding: 5px 12px;
    width: 113px;
    height: 25px;
    background-color: #000000;
    color:#FFFFFF {}
    font-weight: 700;
    font-size: 11px;
    text-transform: uppercase;
    text-align: center;
    margin: 0 auto; }


.title {
    margin-top: 38px;
    color:#222222 {}
    font-weight: 700;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;
    line-height: 30px; }

.subtitle {
    margin-top: 19px;
    color:#222222 {}
    font-size: 17px;
    text-align: center;
    font-weight: 400; }

header {
    position: relative;
    height: 50px; }

nav {
    background-color: #212121;
    height: 51px;
    position: fixed;
    width: 100%;
    z-index: 2; }

.menu {
    display: flex;
    padding: 13.5px 70px;
    margin: 0;
    justify-content: space-between;
    list-style-type: none;

    .menu_link {
        color: #FFFFFF;
        font-size: 15px;
        font-weight: 300; }

    .menu_item {
        border-right: 2px dotted black;
        padding-right: 28px; } }

.subheader {
    position: absolute;
    width: 100%;
    top: 50px;
    background-color: rgba(0, 0, 0, .6);
    height: 85px;

    .subheader_logo {
        display: block;
        margin-top: 16px;
        max-width: 100%; }

    .subheader_logo_text {
        opacity: 0.5;
        color: #FFFFFF;
        font-weight: 100;
        font-size: 13px;
        margin-top: 6px; }

    .subheader_call_as {
        color: #FFFFFF;
        font-weight: 400;
        font-size: 12px;
        margin-top: 20px;
        text-align: right; }

    .subheader_phone {
        display: block;
        margin-top: 8px;
        color: #FFFFFF;
        font-weight: 400;
        font-size: 19px;
        text-transform: uppercase;
        text-align: right;
        line-height: 12px; }

    .subheader_btn {
        @include button;
        margin-top: 20px;
        height: 46px;
        width: 172px;
        color: #FFFFFF;
        font-weight: 400;
        font-size: 14px;
        text-transform: uppercase;
        text-align: center; } }

.promo {
    padding: 150px 0 135px 0;
    min-height: 800px;
    background: url('../img/bg/main_bg.jpg') center center/cover no-repeat;

    .promo_header {
        margin: 0;
        color:#FFFFFF {}
        font-weight: 700;
        font-size: 60px;
        text-align: center;
        text-transform: uppercase;
        line-height: 60px; }

    .promo_subheader {
        margin: 0;
        margin-top: 22px;
        color:#FFFFFF {}
        font-weight: 700;
        font-size: 36px;
        text-align: center;
        text-transform: uppercase;
        line-height: 36px; }

    .promo_descr {
        margin-top: 23px;
        color:#FFFFFF {}
        font-weight: 400;
        font-size: 18px;
        text-align: center;
        line-height: 24px; }

    .promo_btn {
        @include button;
        margin: 0 auto;
        margin-top: 118px;
        width: 259px;
        height: 63px;
        font-size: 18px;
        text-align: center;
        font-weight: 400; } }

.reasons {
    padding: 70px 0 34px 0;
    min-height: 900px;

    .reason_item {
        .reason_block {
            margin-top: 35px;
            display: flex;
            justify-content: center;
            align-items: center;

            .reason_round {
                display: flex;
                justify-content: center;
                align-items: center;
                margin-right: 26px;
                height: 114px;
                width: 114px;
                border-radius: 100%;
                background-color: #1EACC7; }

            .reason_descr {
                width: 341px;

                .reason_subtitle {
                    color:#1EACC7 {}
                    font-weight: 400;
                    font-size: 17px;
                    line-height: 21px; }

                .reason_text {
                    font-weight: 300;
                    font-size: 15px;
                    text-align: justify;
                    line-height: 21px;
                    color: #202020; } } } } }

.mobile {
    min-height: 865px;
    padding: 48px 0 79px 0;
    background: url('../img/bg/mobile_app.png') center center/cover no-repeat;
    &_tabs {
        margin-top: 35px; }
    &_item {
        width: 280px;
        position: relative;
        height: 236px;
        padding: 93px 0 35px 0;
        margin-bottom: 19px;
        @for $i from 1 through 6 {
            &_#{$i} {
                background: url('../img/bg/mobile_screen_#{$i}.jpg') center center/cover no-repeat; } }
        &_title {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            text-transform: uppercase;
            text-align: center;
            line-height: 31px; }

        &_plus {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            height: 27px;
            width: 27px;
            border-radius: 100%;
            margin: 0 auto;
            background-color: #222222;
            cursor: pointer; } } }

.choise {
    min-height: 100%;

    .choise_head {
        padding-top: 63px;
        min-height: 500px;
        background-color: #070716;
        position: relative;

        .car {
            display: block;
            margin: 0 auto;
            position: absolute;
            bottom: -45%;
            left: 50%;
            transform: translateX(-50%); } }

    .lable {
        background-color: #FFFFFF;
        color: #000000; }

    .title {
        color: #FFFFFF; }

    .subtitle {
        color: #FFFFFF; }

    .choise_buttom {
        padding-top: 218px;
        padding-bottom: 138px;
        min-height: 400px;
        background: url('../img/bg/mobile_app.png') center center/cover no-repeat;

        .choise_title {
            color:#222222 {}
            margin: 0 auto;
            font-weight: 200;
            font-size: 17px;
            text-align: center;
            width: 297px; } } }











